@charset "UTF-8";
/* Estilos para las tarjetas de menú */
.menu-section {
  padding: 20px;
  text-align: center;
  width: 90%;
  margin: 4rem auto;
  height: 100%;
  margin-bottom: 2rem;
}

.menu-cards {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  margin: 0 auto;
}

.menu-card {
  border-radius: 10px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0rem;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .menu-cards {
    grid-template-columns: repeat(1, 1fr);
  }
}
.menu-card img {
  max-width: 100%;
  height: 225px; /* Ajusta la altura según tus preferencias */
  object-fit: cover; /* Escala y recorta la imagen para que se ajuste al contenedor */
  border-radius: 15px;
}

.menu-card h3 {
  font-size: 1.6rem;
  text-align: center;
  margin-bottom: 0;
}

.menu-card p {
  font-size: 1.2rem;
  margin: 5px 0;
}

.price {
  font-weight: bold;
  font-size: 1rem;
  color: #FF784F;
  border-radius: 5px;
}

/* Estilos para el contenedor de los botones "Añadir al Carrito" */
.add-to-cart-container {
  margin-top: auto; /* Empuja los botones hacia abajo para que estén siempre a la misma altura */
  text-align: center; /* Centra los botones horizontalmente */
}

/* Estilos para el botón "Añadir al Carrito" */
.add-to-cart {
  width: 50%;
  margin: 5px auto;
  background-color: #FF784F;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.6rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.add-to-cart:hover {
  background-color: #FF784F;
}

/* Estilos para el ícono del carrito en la barra de navegación */
.cart-icon {
  position: relative;
  cursor: pointer;
}

.cart-icon img {
  width: 30px;
  height: 30px;
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #FF784F;
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.eliminar-producto {
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1rem;
  cursor: pointer;
}